import { Component } from 'react';
import './App.css';

const clientId = 'ebcbc13ca3b34ed6a4cf0bf4d7579df9';
const redirect = 'https%3A%2F%2Flocalhost:3000%2F';

class App extends Component {
  constructor(){
    super()
    this.state = {}
  }

  componentDidMount(){
    window.location.replace(`https://accounts.spotify.com/authorize?client_id=${clientId}&response_type=code&redirect_uri=${redirect}&show_dialog=true`)
  }

  render(){
  return (
    <div className="App">
      <p>
        This is working! Hello world!
      </p>
    </div>
  )}
}

export default App;
